import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import DataTable from "../../formElements/DataTable";
import { useNavigate } from "react-router-dom";
import { FaLongArrowAltLeft } from "react-icons/fa";
import { users } from "../../../redux/slices/UserSlice";
import { Roles } from "../../../redux/slices/RolesSlice";

const List = () => {
  const Users = useSelector(users);
  const roles = useSelector(Roles);
  const navigate = useNavigate();
  const modifiedItems = [];

  useMemo(() => {
    const rolesMap = new Map();
    roles.forEach((role) => {
      rolesMap.set(role.id, role.Role);
    });

    Users.forEach((user) => {
      if (rolesMap.has(user.roleid)) {
        modifiedItems.push({
          ...user,
          Role: rolesMap.get(user.roleid),
        });
      }
    });
  }, [roles, Users]);
  const data = modifiedItems.map((Cat) => {
    return {
      id: Cat.id,
      Role: Cat.Role,
      username: Cat.username,
      inactive: Cat.inactive === 1 ? "Yes" : "No",
    };
  });
  const columns = useMemo(
    () => [
      {
        Header: "Username",
        accessor: "username",
      },
      {
        Header: "Role",
        accessor: "Role",
      },
      {
        Header: "Inactive",
        accessor: "inactive",
      },
    ],
    []
  );

  return (
    <>
      <div className="px-7 py-1 overflow-auto flex flex-col   h-[calc(100vh-70px)]">
        <div className="inline-flex items-center">
          <span
            className=" inline-block p-2  text-slate-500 cursor-pointer hover:bg-slate-700 hover:text-slate-300 hover:rounded-lg "
            onClick={() => navigate("../")}
          >
            <FaLongArrowAltLeft></FaLongArrowAltLeft>
          </span>
          <span className=" text-slate-300 text-sm">Back</span>
        </div>
        {modifiedItems.length > 0 ? (
          <DataTable columns={columns} data={data} />
        ) : (
          <p>No Users Found</p>
        )}
      </div>
    </>
  );
};

export default List;
